.login {
    display: flex;
    flex-direction: column;
    align: center;
    align-items: center;
    border-radius: 2px;
    border-color: lightgrey;
    border-style: solid;
    border-width: 1px;
    margin: 10px;
    padding-bottom: 10px;
    width: 400px;
    height: 300px;
  }
  .login__box {
    display: flex;
    flex-direction: column;
    width: 250px;
    hieght: 300px;
    align-items: center;
    font-size: 25px;
  }
  .login__box > button {
    background-color: orange;
    border: 1px solid;
    width: 100%;
    margin-top: 10px;
    width: 100%;
    height: 30px;
    border-radius: 5px;
    color: #111;
  }
  .login__box > input {
    border: 1px solid;
    width: 100%;
    margin-top: 10px;
    width: 100%;
    height: 30px;
    border-radius: 5px;
    color: #111;
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -ms-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    outline: none;
    padding: 3px 0px 3px 3px;
    margin: 5px 1px 3px 0px;
    border: 1px solid #dddddd;
  }
  .login__box > small {
    margin-top: 10px;
    font-size: 10px !important;
  }
  .login__box > label {
    font-size: 20px;
    align-items: left;
  }
  #account:focus,
  textarea:focus {
    box-shadow: 0 0 5px orange;
    padding: 3px 0px 3px 3px;
    margin: 5px 1px 3px 0px;
    border: 1px solid orange;
  }